.search-form__label-for-checkbox {
    display: flex;
    cursor: pointer;
}

.search-form__checkbox {
  position: absolute;
  width: 1px; /*      ,   ,     */
  height: 1px;
  overflow: hidden; /*     ,   .    ,   ,     */
  clip: rect(0 0 0 0);
}

.search-form__checkbox-span {
  display: inline-block; /*  span        */
  /* vertical-align: middle; */
  background-image: url('../../../../images/searchForm/smalltumboff-min-2-01.svg');
  background-size: cover;
  background-repeat: no-repeat;
  width: 36px;
  height: 20px;
  margin: auto 0 auto 29px;
}

.search-form__checkbox-span-visible {
  background-image: url('../../../../images/searchForm/smalltumb-min-1.svg');
  background-size: cover;
  background-repeat: no-repeat;
  width: 36px;
  height: 20px;
  margin: auto 0 auto 29px;
}

