  background-size: cover;
  background-repeat: no-repeat;
  width: 36px;
  height: 20px;
} */

.search-form__checkbox-text {
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 138%;
  color: #ffffff;
  margin: auto 30px auto 14px;
  transition: opacity 0.4s ease;
}
.search-form__checkbox-text:hover {
    opacity: 0.7;
}

@media screen and (max-width: 600px) {
  .search-form__checkbox-text {
    font-size: 11px;
    line-height: 164%;
    margin-right: auto;
  }

  .search-form__checkbox-span {
    margin-left: auto;
  }

  .search-form__checkbox-span-visible {
    margin-left: auto;
  }
}