.navtab {
  background-color: #073042;
  margin: 0;
}
.navtab__container {
  max-width: 1280px;
  min-height: 50px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 950px) {
  .navtab__container {
    min-height: 66px;
  }
}

@media screen and (max-width: 550px) {
  .navtab__container {
    min-height: 0;
  }
}
